  /* max-width: 1000px;
  display: flex; */
}

.smoothie__wrap {
  justify-content: center;
  display: grid;
  grid-template-columns: 250px 250px 250px 250px;
  column-gap: 2px;
}

.smoothie__box {
  text-align: center;
  border-bottom: 1px solid #f6edde;
  border-right: 1px solid #f6edde;
  padding: 20px 0 0 0;
}

.smoothie__box:nth-of-type(4n) {
  border-right: none;
}

.smoothie__box.smoothie__box:nth-last-of-type(-n + 4) {
  border-bottom: none;
}

.smoothie__img {
  width: 79px;
  height: 68px;
